:host ::ng-deep {
  .wizard-dialog .wizard-content {
    @apply w-[70vw] h-auto max-w-[800px] min-w-[400px] max-h-[90vh] overflow-hidden;
  }

  // Reset some Dialog padding to give more space
  .p-dialog-header {
    @apply py-3 px-4;
  }

  .p-dialog-footer {
    @apply py-3 px-4;
  }
}

.wizard-content {
  @apply h-auto max-w-[800px] min-w-[400px] max-h-[90vh] overflow-hidden;
}
